import React, { Component } from 'react';

import {Button, Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";

export default class VideoBlock extends Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
        };

        this.toggle = this.toggle.bind(this);
    }

    toggle() {
        this.setState({
            modal: !this.state.modal
        });
    }
    render() {
        return (
            <div className="video-block">
                <div className="image-container image-rounded mb-3" onClick={this.toggle}>
                    <img src={this.props.img} alt=""/>
                </div>
                <h4 onClick={this.toggle}>
                    {this.props.title}
                </h4>
                <p className="color-red">
                    {this.props.date}
                </p>
                <Modal style={{maxWidth: '720px', width: 'auto'}} isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                    <span className="icon-close" onClick={this.toggle}/>
                    <ModalBody>
                        <iframe width="100%" height="400px" src={this.props.src} frameborder="0" allow="autoplay; encrypted-media" allowfullscreen=""/>
                    </ModalBody>
                </Modal>
            </div>
        )
    }
}


