import React, { Component } from 'react';
import Slider from "react-slick";

import './artists.css';
import i12 from '../../assets/img/12.svg';
import SocialLinks from "../../components/socialLinksComponent/socialLinks";
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Button, Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";

class Artists extends Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
            modalImg: ''
        };

        this.toggle = this.toggle.bind(this);

        this.next = this.next.bind(this);
        this.prev = this.prev.bind(this);
    }

    toggle(src = '') {
        this.setState({
            modalImg: src,
            modal: !this.state.modal
        });
    }

    next() {
        this.slider.slickNext();
    }
    prev() {
        this.slider.slickPrev();
    }

    componentDidMount() {
        this.props.onMount();
    }
    componentWillUnmount() {
        this.props.onUnmount();
    }

    render() {
        const artists = [
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/az-gray-min.jpg?alt=media&token=810da869-8175-4b9b-b86e-db0800308d64',
                name: 'A.Z',
                full_name: ' ',
                position: '  Ninety One, '
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/alem-gray-min.jpg?alt=media&token=ba928d3f-e947-45f3-a473-6f1cf6ef430f',
                name: 'Alem',
                full_name: ' ',
                position: '  Ninety One'
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/bala-gray-min.jpg?alt=media&token=999962b9-943a-49e8-9b76-9e3da3d19325',
                name: 'Bala',
                full_name: ' ',
                position: '  Ninety One'
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/zaq-gray-min.jpg?alt=media&token=76de4385-ea94-46c8-a10a-46a6f3a3b03e',
                name: 'ZaQ',
                full_name: ' ',
                position: ', '
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/alem-gray-min.jpg?alt=media&token=ba928d3f-e947-45f3-a473-6f1cf6ef430f',
                name: 'Alem',
                full_name: ' ',
                position: '  Ninety One'
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/bala-gray-min.jpg?alt=media&token=999962b9-943a-49e8-9b76-9e3da3d19325',
                name: 'Bala',
                full_name: ' ',
                position: '  Ninety One'
            }
        ];
        const settings = {
            infinite: true,
            centerMode: true,
            slidesToShow: 3,
            centerPadding: '0',
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            focusOnSelect: true,
            responsive: [
                {
                    breakpoint:  992,
                    settings: {
                        vertical: false,
                        verticalSwiping: false,
                        centerMode: true,
                        variableWidth: true,
                        centerPadding: '25px',
                        slidesToShow: 4,
                    }
                },
                {
                    breakpoint:  768,
                    settings: {
                        vertical: false,
                        verticalSwiping: false,
                        centerMode: true,
                        variableWidth: true,
                        centerPadding: '20px',
                        slidesToShow: 3,
                    }
                },
                {
                    breakpoint:  576,
                    settings: {
                        vertical: false,
                        verticalSwiping: false,
                        centerMode: true,
                        variableWidth: true,
                        centerPadding: '15px',
                        slidesToShow: 2,
                    }
                },
                {
                    breakpoint:  360,
                    settings: {
                        vertical: false,
                        verticalSwiping: false,
                        centerMode: true,
                        variableWidth: true,
                        centerPadding: '5px',
                        slidesToShow: 1,
                    }
                }
            ]
        };
        const gallerySlider = {
            slidesToShow: 4,
            slidesToScroll: 1,
            arrows: false
        };

        const getArtists = () => {
            let rows = [];
            for (let i in artists) {
                const artist = artists[i];
                rows.push(
                    <div key={i}>
                        <div className="artist-item d-block d-lg-flex">
                            <div className="artist-photo">
                                <div className="image-container image-circle mr-lg-4" title={artist.name}>
                                    <img src={artist.img} alt=""/>
                                </div>
                            </div>
                            <div className="artist-info mt-4">
                                <h3 className="big">
                                    <span className="text-uppercase"> { artist.name } </span> - { artist.full_name }
                                </h3>
                                <p className="">
                                    { artist.position }
                                </p>
                            </div>
                        </div>
                    </div>
                )
            }
            return rows;
        };
        return (
            <div className="artists-page" >
                <section className="bg-lined">
                    <div className="artists-bg-image">
                        NINETY ONE
                    </div>
                    <div className="container" >
                        <div className="row align-items-center pt-lg-150 pb-lg-70">
                            <div className="col-lg-5 artists-jumbotron">
                                <h1 className="mb-4">
                                    <span className="text-uppercase">
                                         Ninety One
                                    </span>
                                </h1>
                                <p className="big" style={{maxWidth: '348px'}}>
                                    :     ORDA -  
                                </p>
                            </div>
                            <div className="col-lg-7">
                                <Slider {...settings} className="artists-list">
                                    { getArtists() }
                                </Slider>
                            </div>
                        </div>
                    </div>
                    <div className="container">
                        <div className="row">
                            <div className="col-md-5">
                                <div className="image-container minify-image" style={{width: '100%', height: '450px'}}>
                                    <img src="http://goodhouse.kz/wp-content/uploads/2017/08/3-730x1024.jpg" alt=""/>
                                </div>
                            </div>
                            <div className="col-md-7 py-5">
                                <h3> </h3>
                                <p className="mb-20">   Ninety One</p>
                                <SocialLinks dark/>
                                <div className="row mt-50 mx-0 mb-3">
                                    <h4>
                                        <span className="text-uppercase">
                                             
                                        </span>
                                    </h4>
                                    <div className="ml-auto d-flex align-items-center">
                                        <span onClick={this.prev} className="font-24 d-inline-block mr-3 icon-arrow-circle-hover-full-left" style={{cursor: 'pointer'}}/>
                                        5 
                                        <span onClick={this.next} className="font-24 d-inline-block ml-3 icon-arrow-circle-hover-full-right" style={{cursor: 'pointer'}}/>
                                    </div>
                                </div>

                                <Slider ref={c => (this.slider = c)} {...gallerySlider} className="row">
                                    <div className="col-12">
                                        <div className="image-container image-square minify-image" onClick={ () => { this.toggle('https://pm1.narvii.com/6451/6625d1fe98de130bee7054b97501c68945bd713c_hq.jpg') }}>
                                            <img src="https://pm1.narvii.com/6451/6625d1fe98de130bee7054b97501c68945bd713c_hq.jpg" alt=""/>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="image-container image-square minify-image"  onClick={ () => { this.toggle('https://pp.vk.me/c836629/v836629300/62f0/kW6zCyx8XS8.jpg') }}>
                                            <img src="https://pp.vk.me/c836629/v836629300/62f0/kW6zCyx8XS8.jpg" alt=""/>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="image-container image-square minify-image" onClick={ () => { this.toggle('http://91ninetyone.kz/img/alem.jpg') }}>
                                            <img src="http://91ninetyone.kz/img/alem.jpg" alt=""/>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="image-container image-square minify-image" onClick={ () => { this.toggle('https://s-media-cache-ak0.pinimg.com/originals/28/dd/2a/28dd2ac8b1bbbfd4c3ff6b31f6b5e1cb.jpg') }}>
                                            <img src="https://s-media-cache-ak0.pinimg.com/originals/28/dd/2a/28dd2ac8b1bbbfd4c3ff6b31f6b5e1cb.jpg" alt=""/>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="image-container image-square minify-image" onClick={ () => { this.toggle('https://scontent-sea1-1.cdninstagram.com/vp/5df3e8c996e1785abe6604f0f3dcd391/5B6B7F70/t51.2885-15/e35/14717454_1834713400073739_7666909063111770112_n.jpg?ig_cache_key=MTM3NDA4NjE2MjQ0MDYyNTA4Mw%3D%3D.2') }}>
                                            <img src="https://scontent-sea1-1.cdninstagram.com/vp/5df3e8c996e1785abe6604f0f3dcd391/5B6B7F70/t51.2885-15/e35/14717454_1834713400073739_7666909063111770112_n.jpg?ig_cache_key=MTM3NDA4NjE2MjQ0MDYyNTA4Mw%3D%3D.2" alt=""/>
                                        </div>
                                    </div>
                                </Slider>
                            </div>
                        </div>
                    </div>
                    <div className="bg-gray my-50  py-5">
                        <div className="container">
                            <h3>
                                <span className="text-uppercase"> </span>
                            </h3>
                            <br/>
                            <p>
                                     ,  18  1993    , .      .<br/>
                                <br/>
                                       , .      ,     The Voice.
                            </p>
                            <br/>
                            <br/>
                            <br/>
                            <h4>
                                <span className="text-uppercase"></span>
                            </h4>
                            <br/>
                            <p>
                                  -.    ,        (),  ()    [3].   .
                            </p>
                        </div>
                    </div>
                </section>
                <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                    <span className="icon-close" onClick={this.toggle}/>
                    <ModalBody>
                        <img width="100%" src={this.state.modalImg} alt=""/>
                    </ModalBody>
                </Modal>
            </div>
        );
    }
}


export default withRouter(connect(
    state => ({
        header: state.header
    }),
    dispatch => ({
        onMount: () => {
            dispatch({type: 'SET_STYLE', payload: 'DARK'});
            dispatch({type: 'SET_OPACITY', payload: 'ALWAYS_FULL'})
        },
        onUnmount: () => {
            dispatch({type: 'SET_STYLE', payload: 'DEFAULT'});
            dispatch({type: 'SET_OPACITY', payload: 'DEFAULT'})
        }
    })
)(Artists));



